<script>
  import { getContext } from 'svelte';
  import Button from '../Button';
  import { ITEM_GROUP } from '../ItemGroup/ItemGroup.svelte';

  const { select, register, index, _activeClass } = getContext(ITEM_GROUP);

  let active;

  let klass = '';
  export { klass as class };
  export let value = index();
  export let activeClass = _activeClass;
  export let disabled = null;

  register((values) => {
    active = values.includes(value);
  });

  function click() {
    if (!disabled) select(value);
  }
</script>

<style lang="scss" src="./ButtonGroupItem.scss" global>/* prettier-ignore */
:global(.s-btn-group-item) {
  border-radius: 0;
  border-style: solid;
  border-width: thin;
  opacity: 0.8;
  padding: 0 12px;
}
:global(.s-btn-group-item:first-child) {
  border-top-left-radius: inherit;
  border-bottom-left-radius: inherit;
}
:global(.s-btn-group-item:last-child) {
  border-top-right-radius: inherit;
  border-bottom-right-radius: inherit;
}
:global(.s-btn-group-item:not(:first-child)) {
  border-left-width: 0;
}</style>

<Button
  class="s-btn-group-item {klass}"
  {activeClass}
  {active}
  {disabled}
  on:click={click}
  on:click>
  <slot />
</Button>
