<script>
  import { getContext } from 'svelte';
  import Ripple from '../../actions/Ripple';
  import Class from '../../internal/Class';

  const role = getContext('S_ListItemRole');

  let klass = '';
  export { klass as class };
  export let activeClass = 'active';
  export let value = null;
  export let active = false;
  export let dense = false;
  export let disabled = null;
  export let selectable = !role;
  export let multiline = false;
  export let ripple = getContext('S_ListItemRipple') || !selectable || false;

  const Group = getContext('S_ListItemGroup');
  let click = () => {};

  if (Group) {
    const { select, register, index } = getContext(Group);
    value = value || index();
    register((values) => {
      active = values.includes(value);
    });
    click = () => {
      if (!disabled) select(value);
    };
  }
</script>

<style lang="scss" src="./ListItem.scss" global>/* prettier-ignore */
:global(.s-list-item__content) {
  align-items: center;
  align-self: center;
  display: flex;
  flex-wrap: wrap;
  flex: 1 1;
  overflow: hidden;
  padding: 12px 0;
}
:global(.s-list-item__content) > :global(*) {
  line-height: 1.1;
  flex: 1 0 100%;
}
:global(.s-list-item__content) > :global(*:not(:last-child)) {
  margin-bottom: 2px;
}

:global(.s-list-item__title),
:global(.s-list-item__subtitle) {
  flex: 1 1 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  line-height: 1.2;
}

:global(.s-list-item__title) {
  align-self: center;
  font-size: 1rem;
}

:global(.s-list-item__subtitle) {
  font-size: 0.875rem;
  color: var(--theme-text-secondary);
}

:global(.s-list-item) {
  align-items: center;
  display: flex;
  flex: 1 1 100%;
  letter-spacing: normal;
  min-height: 48px;
  outline: none;
  padding: 0 16px;
  position: relative;
  text-decoration: none;
}
:global(.s-list-item) :global(.s-avatar) {
  align-self: center;
  margin-top: 8px;
  margin-bottom: 8px;
}
:global(.s-list-item) :global(.s-icon) {
  align-self: flex-start;
  margin-top: 16px;
  margin-bottom: 16px;
}
:global(.s-list-item) :global(.s-icon),
:global(.s-list-item) :global(.s-avatar) {
  display: inline-flex;
  min-width: 24px;
}
:global(.s-list-item) :global([slot=prepend]) :global(.s-avatar) {
  margin-right: 16px;
}
:global(.s-list-item) :global([slot=prepend]) :global(.s-icon),
:global(.s-list-item) :global([slot=prepend]) :global(.s-checkbox),
:global(.s-list-item) :global([slot=prepend]) :global(.s-radio) {
  margin-right: 32px;
}
:global(.s-list-item.link) {
  cursor: pointer;
  user-select: none;
}
:global(.s-list-item.link::before) {
  background-color: currentColor;
  bottom: 0;
  top: 0;
  left: 0;
  right: 0;
  content: "";
  pointer-events: none;
  position: absolute;
  opacity: 0;
  transition: all 0.3s cubic-bezier(0.25, 0.8, 0.5, 1) 0s;
}
:global(.s-list-item.link:not(.active):hover::before) {
  opacity: 0.04;
}
:global(.s-list-item.selectable) {
  user-select: auto;
}
:global(.s-list-item.multiline) :global(.s-list-item__title),
:global(.s-list-item.multiline) :global(.s-list-item__subtitle) {
  white-space: normal;
}
:global(.s-list-item:not(.disabled).focus-visible::before) {
  opacity: 0.12;
}
:global(.s-list-item.disabled) {
  pointer-events: none;
  color: var(--theme-text-disabled);
}
:global(.s-list-item.flat::before) {
  display: none;
}
:global(.s-list-item.active::before) {
  opacity: 0.12;
}
:global(.s-list-item.active) :global(.s-icon) {
  color: inherit;
}
:global(.s-list-item::after) {
  content: "";
  min-height: inherit;
  font-size: 0;
}
:global(.s-list-item:not(.active)) {
  color: var(--theme-text-primary);
}

:global(.s-list-item.dense),
:global(.s-list.dense) :global(.s-list-item) {
  min-height: 40px;
}
:global(.s-list-item.dense) :global(.s-icon),
:global(.s-list.dense) :global(.s-list-item) :global(.s-icon) {
  margin-top: 8px;
  margin-bottom: 8px;
}
:global(.s-list-item.dense) :global([slot=prepend]) :global(.s-icon),
:global(.s-list-item.dense) :global([slot=prepend]) :global(.s-checkbox),
:global(.s-list-item.dense) :global([slot=prepend]) :global(.s-radio),
:global(.s-list.dense) :global(.s-list-item) :global([slot=prepend]) :global(.s-icon),
:global(.s-list.dense) :global(.s-list-item) :global([slot=prepend]) :global(.s-checkbox),
:global(.s-list.dense) :global(.s-list-item) :global([slot=prepend]) :global(.s-radio) {
  margin-right: 24px;
}
:global(.s-list-item.dense) :global(.s-list-item__content),
:global(.s-list.dense) :global(.s-list-item) :global(.s-list-item__content) {
  padding: 8px 0;
}
:global(.s-list-item.dense) :global(.s-list-item__title),
:global(.s-list-item.dense) :global(.s-list-item__subtitle),
:global(.s-list.dense) :global(.s-list-item) :global(.s-list-item__title),
:global(.s-list.dense) :global(.s-list-item) :global(.s-list-item__subtitle) {
  font-size: 0.8125rem;
  font-weight: 500;
  line-height: 1rem;
}</style>

<div
  class="s-list-item {klass}"
  use:Class={[active && activeClass]}
  {role}
  aria-selected={role === 'option' ? active : null}
  class:dense
  class:disabled
  class:selectable
  class:multiline
  class:link={!selectable}
  use:Ripple={ripple}
  on:click={click}
  on:click>
  <slot name="prepend" />
  <div class="s-list-item__content">
    <div class="s-list-item__title">
      <slot />
    </div>
    <div class="s-list-item__subtitle">
      <slot name="subtitle" />
    </div>
  </div>
  <slot name="append" />
</div>
