<script context="module">
  export const SLIDE_GROUP = {};
  const prevIcon = 'M15.41,16.58L10.83,12L15.41,7.41L14,6L8,12L14,18L15.41,16.58Z';
  const nextIcon = 'M8.59,16.58L13.17,12L8.59,7.41L10,6L16,12L10,18L8.59,16.58Z';
</script>

<script>
  import { setContext, afterUpdate } from 'svelte';
  import ItemGroup from '../ItemGroup';
  import Icon from '../Icon';

  let contentWidth;
  let wrapperWidth;

  let klass = '';
  export { klass as class };
  export let showArrows = true;
  export let hideDisabledArrows = false;
  export let centerActive = false;
  export let activeClass = '';
  export let value = [];
  export let multiple = false;
  export let mandatory = false;
  export let max = Infinity;

  let x = 0;
  setContext(SLIDE_GROUP, (target) => {
    const item = target.closest('.s-slide-item');
    const left = item.offsetLeft;
    const width = item.offsetWidth;

    if (centerActive) x = left + (width - wrapperWidth) / 2;
    else if (left + 1.25 * width > wrapperWidth + x) {
      x = left + 1.25 * width - wrapperWidth;
    } else if (left < x + width / 4) {
      x = left - width / 4;
    }
  });

  afterUpdate(() => {
    if (x + wrapperWidth > contentWidth) x = contentWidth - wrapperWidth;
    else if (x < 0) x = 0;
  });

  function next() {
    x += wrapperWidth;
  }

  function prev() {
    x -= wrapperWidth;
  }

  let touchStartX;
  function touchstart({ touches }) {
    touchStartX = x + touches[0].clientX;
  }

  function touchmove({ touches }) {
    x = touchStartX - touches[0].clientX;
  }

  $: arrowsVisible = wrapperWidth < contentWidth && showArrows;
</script>

<style lang="scss" src="./SlideGroup.scss" global>/* prettier-ignore */
:global(.s-slide-group) {
  display: flex;
}

:global(.s-slide-group__content) {
  display: flex;
  flex: 1 0 auto;
  position: relative;
  transition: 0.3s cubic-bezier(0.25, 0.8, 0.5, 1);
  white-space: nowrap;
}

:global(.s-slide-group__wrapper) {
  contain: content;
  display: flex;
  flex: 1 1 auto;
  overflow: hidden;
  touch-action: none;
}

:global(.s-slide-group__next),
:global(.s-slide-group__prev) {
  align-items: center;
  display: flex;
  flex: 0 1 52px;
  justify-content: center;
  min-width: 52px;
  cursor: pointer;
}
:global(.s-slide-group__next.disabled),
:global(.s-slide-group__prev.disabled) {
  pointer-events: none;
  opacity: 0.5;
}
:global(.s-slide-group__next.disabled.hide-disabled-arrows),
:global(.s-slide-group__prev.disabled.hide-disabled-arrows) {
  visibility: hidden;
  opacity: 0;
}</style>

<ItemGroup
  class="s-slide-group {klass}"
  on:change
  bind:value
  {activeClass}
  {multiple}
  {mandatory}
  {max}>
  {#if arrowsVisible}
    <div
      class="s-slide-group__prev"
      class:disabled={x === 0}
      class:hide-disabled-arrows={hideDisabledArrows}
      on:click={prev}>
      <slot name="previous">
        <Icon path={prevIcon} />
      </slot>
    </div>
  {/if}
  <div
    class="s-slide-group__wrapper"
    on:touchstart|passive={touchstart}
    on:touchmove|passive={touchmove}
    bind:clientWidth={wrapperWidth}>
    <div
      class="s-slide-group__content"
      style="transform:translate(-{x}px)"
      bind:clientWidth={contentWidth}>
      <slot />
    </div>
  </div>
  {#if arrowsVisible}
    <div
      class="s-slide-group__next"
      class:disabled={x === contentWidth - wrapperWidth}
      class:show-arrows={hideDisabledArrows}
      on:click={next}>
      <slot name="next">
        <Icon path={nextIcon} />
      </slot>
    </div>
  {/if}
</ItemGroup>
