<script>
  import { slide } from 'svelte/transition';
  import { onMount, setContext } from 'svelte';
  import Style from '../../internal/Style';

  let klass = 'primary-text';
  export { klass as class };
  export let active = true;
  export let eager = false;
  export let transition = slide;
  export let transitionOpts = {};
  export let offset = null;
  export let disabled = null;
  export let ripple = {};
  export let style = null;

  setContext('S_ListItemRipple', ripple);

  function toggle() {
    active = !active;
  }

  if (eager) {
    const tempActive = active;
    active = true;
    onMount(() => {
      active = tempActive;
    });
  }
</script>

<style lang="scss" src="./ListGroup.scss" global>/* prettier-ignore */
:global(.s-list-group) {
  flex: 0 1 auto;
  position: relative;
  max-width: 100%;
}
:global(.s-list-group) :global(.s-list-item.active) {
  color: inherit;
}
:global(.s-list-group.disabled) {
  pointer-events: none;
  color: var(--theme-text-disabled);
}
:global(.s-list-group.offset) :global(.s-list-item) {
  padding-left: var(--s-list-group-offset) !important;
}</style>

<slot name="activator" {toggle} {active} />
{#if active}
  <div
    transition:transition={transitionOpts}
    role="group"
    aria-disabled={disabled}
    class="s-list-group {klass}"
    class:offset
    {style}
    use:Style={{ 'list-group-offset': offset }}>
    <slot />
  </div>
{/if}
